
  .vendasTable {
    background-color: var(--third-color);
    width: 100%;
    border-collapse: collapse;
    border-radius: 5px;
    overflow: hidden;
    font-size: 14px;
    margin-top: 20px;
  }
  
  .vendasTable th, .vendasTable td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid var(--background-color);
  }
  
  .vendasTable th {
    background-color: var(--container-color);
    color: var(--text-color);
  }
  
  .vendasTable tr:hover {
    background-color: var(--hover-color);
    color: var(--text-color);
  }
  
  .vendaRow {
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .vendaRow:hover {
    background-color: var(--hover-color); /* Uma cor mais clara para o hover */
  }
  
  .vendaDetails {
    background-color: var(--secondary-color);
    animation: slideDown 0.3s ease-in-out;
  }
  
  @keyframes slideDown {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .detailsContainer {
    padding: 10px;
    background-color: var(--secondary-color);
    border-radius: 5px;
    color: var(--text-color);
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .vendaImage {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  /* Ícones */
  .icon {
    margin-right: 8px;
    vertical-align: middle;
  }
  
  .iconPositive {
    color: green;
  }
  
  .iconNegative {
    color: red;
  }
  
  /* Lucro positivo e negativo */
  .lucroPositivo {
    color: green;
  }
  
  .lucroNegativo {
    color: red;
  }
  